.dropdown-content {
  display: block;
}

.dropdown:hover .dropdown-content {
  display: block;
  
}
.links {
  display: flex;
  justify-content: center;
  align-items: center;
}

.links a {
  font-weight: 700;
  position: relative;
  color: white;
  padding: 10px;
  border-bottom: 5px solid transparent;
}

.links a:hover {
  
  transition: border-color 0.3s ease;
}

.dropdown {
  position: relative;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  top: 100%;
  left: 0;
  margin-top: 5px;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}
